  z-index: 499;
  width: 100%;
  padding: 10px;
}

.node-preview-backlink {
  background: #419ff1 url(../../../../misc/icons/000000/chevron-left.svg) left no-repeat, linear-gradient(to bottom, #419ff1, #1076d5);
  /* LTR */
