}

.node-preview-backlink:active {
  background: #0e69be url(../../../../misc/icons/000000/chevron-left.svg) left no-repeat, linear-gradient(to bottom, #0e69be, #2a93ef);
  /* LTR */
  border: 1px solid #0048c8;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.25);
}

[dir="rtl"] .node-preview-backlink:active {
  background: url(../../../../misc/icons/000000/chevron-right.svg) right no-repeat, linear-gradient(to bottom, #0e69be, #2a93ef);
}

.node-preview-backlink::before {
  content: '';
  width: 10px;
  display: inline-block;
}

/**
 * @file
 * Visual styles for Droopler's node component.
 */
.node__content {
  font-size: 1.071em;
  margin-top: 10px;
}

/* View mode teaser styles. */
.node--view-mode-teaser {
  border-bottom: 1px solid #d3d7d9;
  margin-bottom: 30px;
  padding-bottom: 15px;
}

.node--view-mode-teaser h2 {
  margin-top: 0;
  padding-top: 0.5em;
}

.node--view-mode-teaser h2 a {
  color: #181818;
}

.node--view-mode-teaser.node--sticky {
  background: #f9f9f9;
  background: rgba(0, 0, 0, 0.024);
  border: 1px solid #d3d7d9;
  padding: 0 15px 15px;
}

.node--view-mode-teaser .node__content {
  clear: none;
  font-size: 1em;
  line-height: 1.6;
}

.node--view-mode-thumbnail h2 {
  margin-top: 0;
  padding-top: 0.5em;
}

.node--view-mode-thumbnail h2 a {
  color: #000;
}

.node--view-mode-thumbnail .node__meta {
  padding-left: 0;
}

.node--view-mode-thumbnail .node__content {
  clear: none;
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 15px;
}

.node--view-mode-thumbnail .node__content .field--type-image {
  float: none;
}

.node--view-mode-thumbnail .nav-link {
  padding-left: 0;
}

.node--view-mode-thumbnail .nav-link.node-readmore {
  text-transform: uppercase;
}

/* Node metadata styles. */
.node__meta {
  font-size: 0.857em;
  color: #68696b;
  margin-bottom: -5px;
}

.node__meta .field--name-field-user-picture img {
  float: left;
  /* LTR */
  margin: 1px 20px 0 0;
  /* LTR */
}

[dir="rtl"] .node__meta .field--name-field-user-picture img {
  float: right;
  margin-left: 20px;
  margin-right: 0;
}

/* Node links styles. */
.node__links {
  text-align: right;
  /* LTR */
  font-size: 0.93em;
}

[dir="rtl"] .node__links {
  text-align: left;
}

/* Unpublished node styles. */
.node--unpublished {
  padding: 20px 15px 0;
}

.node--unpublished .comment-text .comment-arrow {
  border-left: 1px solid #fff4f4;
  border-right: 1px solid #fff4f4;
}

/**
 * @file
 * The visual styles for Droopler's search form(s).
 */
input[type="search"] {
  box-sizing: border-box;
}

/**
 * @file
 * Visual styles for Bartik's search results.
 */
.search-results {
  padding: 0;
  list-style-position: inside;
}

.search-results li {
  border-bottom: 1px solid #d3d7d9;
  padding-bottom: 0.4285em;
  margin-bottom: 0.5em;
}

.search-results li:last-child {
  border-bottom: none;
  padding-bottom: 0;
  margin-bottom: 1em;
}

.search-result__title {
  font-weight: bold;
}

.search-result__snippet-info {
  padding-left: 0;
  /* LTR */
}

[dir="rtl"] .search-result__snippet-info {
  padding-right: 0;
}

/**
 * @file
 * Visual styles for the Shortcut action link in Droopler.
 */
.shortcut-wrapper {
  margin: 2.2em 0 1.1em 0;
  /* Same as usual .page-title margin. */
}

.shortcut-wrapper .page-title {
  float: left;
  /* LTR */
  margin: 0;
}

[dir="rtl"] .shortcut-wrapper .page-title {
  float: right;
}

.shortcut-action {
  border-bottom: none;
  margin-left: 0.5em;
  /* LTR */
  padding-top: 0.35em;
}

[dir="rtl"] .shortcut-action {
  margin-left: 0;
  margin-right: 0.5em;
}

.shortcut-action:hover,
.shortcut-action:active,
.shortcut-action:focus {
  border-bottom: none;
}

/**
 * @file
 * Footer Styling.
 */
.site-footer {
  font-family: "Lato", sans-serif;
  padding: 35px 0 30px;
}

.site-footer .layout-container {
  padding: 0 15px;
}

@media all and (min-width: 560px) {
  .site-footer__top .region {
    float: left;
    /* LTR */
    position: relative;
    width: 50%;
  }
  [dir="rtl"] .site-footer__top .region {
    float: right;
  }
}

@media all and (min-width: 560px) and (max-width: 850px) {
  .site-footer .region {
    box-sizing: border-box;
  }
  .site-footer__top .region:nth-child(2n+1) {
    padding-right: 10px;
    /* LTR */
  }
  [dir="rtl"] .site-footer__top .region:nth-child(2n+1) {
    padding-left: 10px;
    padding-right: 0;
  }
  .site-footer__top .region:nth-child(2n) {
    padding-left: 10px;
    /* LTR */
  }
  [dir="rtl"] .site-footer__top .region:nth-child(2n) {
    padding-left: 0;
    padding-right: 10px;
  }
  .region-footer-third {
    clear: both;
  }
}

@media all and (min-width: 851px) {
  .site-footer__top .region {
    width: 24%;
    padding: 0 0.65%;
  }
  .site-footer__top .region:first-child {
    padding-left: 0;
    /* LTR */
  }
  [dir="rtl"] .site-footer__top .region:first-child {
    padding-left: 10px;
    padding-right: 0;
  }
  .site-footer__top .region:last-child {
    padding-right: 0;
    /* LTR */
  }
  [dir="rtl"] .site-footer__top .region:last-child {
    padding-left: 0;
    padding-right: 10px;
  }
}

.site-footer h2 {
  color: #c0c0c0;
  color: rgba(255, 255, 255, 0.65);
}

.site-footer blockquote {
  color: #555;
}

.site-footer .content {
  color: #c0c0c0;
  color: rgba(255, 255, 255, 0.65);
  font-size: 0.857em;
}

.site-footer .menu-item {
  padding: 0;
}

.site-footer .content ol:not(.menu),
.site-footer .content ul:not(.menu) {
  padding-left: 1.4em;
  /* LTR */
}

[dir="rtl"] .site-footer .content ol:not(.menu),
[dir="rtl"] .site-footer .content ul:not(.menu) {
  padding-right: 1.4em;
  padding-left: 0;
}

.site-footer .content a,
.site-footer .content a.is-active {
  color: #fcfcfc;
  color: rgba(255, 255, 255, 0.8);
}

.site-footer .content a:hover,
.site-footer .content a:focus {
  color: #fefefe;
  color: rgba(255, 255, 255, 0.95);
}

.site-footer .block {
  margin: 20px 0;
  border: 1px solid #444;
  border-color: rgba(255, 255, 255, 0.1);
  padding: 10px;
}

.site-footer table {
  font-size: 1em;
}

.site-footer tr td,
.site-footer tr th {
  border-color: #555;
  border-color: rgba(255, 255, 255, 0.18);
}

.site-footer tr.odd {
  background-color: transparent;
}

.site-footer tr.even {
  background-color: #2c2c2c;
  background-color: rgba(0, 0, 0, 0.15);
}

/* Footer Top Styling. */
.site-footer__top h2 {
  border-bottom: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
  font-size: 1em;
  margin-bottom: 0;
  padding-bottom: 3px;
  text-transform: uppercase;
}

.site-footer__top .content {
  margin-top: 0;
}

.site-footer__top p {
  margin-top: 1em;
}

.site-footer__top .content .menu {
  padding-left: 0;
  /* LTR */
}

[dir="rtl"] .site-footer__top .content .menu {
  padding-right: 0;
}

.site-footer__top .content li a {
  display: block;
  border-bottom: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
  line-height: 1.2;
  padding: 0.8em 2px 0.8em 20px;
  /* LTR */
  text-indent: -15px;
}

[dir="rtl"] .site-footer__top .content li a {
  padding: 0.8em 20px 0.8em 2px;
}

.site-footer__top .content li a:hover,
.site-footer__top .content li a:focus {
  background-color: #1f1f21;
  background-color: rgba(255, 255, 255, 0.05);
  text-decoration: none;
}

.site-footer__top .block-menu,
.site-footer__bottom .block {
  margin: 0;
  padding: 0;
  border: none;
}

/* Footer Bottom Styling. */
.site-footer__bottom .block {
  margin: 0.5em 0;
}

.site-footer__bottom .content {
  padding: 0.5em 0;
  margin-top: 0;
}

.site-footer__bottom .block h2 {
  margin: 0;
}

.site-footer__bottom {
  letter-spacing: 0.2px;
  margin-top: 30px;
  border-top: 1px solid #555;
  border-color: rgba(255, 255, 255, 0.15);
}

.site-footer__bottom .region {
  margin-top: 20px;
}

.site-footer__bottom .block {
  clear: both;
}

/* We need to be specific to overwrite [dir="rtl"] .block ul in block.css. */
.site-footer__bottom .block .menu {
  padding: 0;
}

.site-footer__bottom .menu-item a {
  float: left;
  /* LTR */
  padding: 0 12px;
  display: block;
  border-right: 1px solid #555;
  /* LTR */
  border-color: rgba(255, 255, 255, 0.15);
}

[dir="rtl"] .site-footer__bottom .menu-item a {
  float: right;
  border-left: 1px solid #555;
  border-right: none;
}

.site-footer__bottom .menu-item:first-child a {
  padding-left: 0;
  /* LTR */
}

[dir="rtl"] .site-footer__bottom .menu-item:first-child a {
  padding-right: 0;
  padding-left: 12px;
}

.site-footer__bottom .menu-item:last-child a {
  padding-right: 0;
  /* LTR */
  border-right: none;
  /* LTR */
}

[dir="rtl"] .site-footer__bottom .menu-item:last-child a {
  padding-left: 0;
  padding-right: 12px;
  border-left: none;
}

/**
 * When the item is both first and last, :last-child overrides the right padding
 * when using [dir="rtl"]. We need this rule to remove the right padding.
 */
[dir="rtl"] .site-footer__bottom .menu-item:first-child:last-child a {
  padding-right: 0;
}

/**
 * @file
 * Styles for the skip link.
 */
.skip-link {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 50;
  background: #444;
  background: rgba(0, 0, 0, 0.6);
  font-size: 0.94em;
  line-height: 1.7em;
  padding: 1px 10px 2px;
  border-radius: 0 0 10px 10px;
  border-bottom-width: 0;
  outline: 0;
}

.skip-link.visually-hidden.focusable:focus {
  position: absolute !important;
  color: #fff;
}

/**
 * @file
 * Styles for Droopler's tables.
 */
table {
  border: 0;
  border-spacing: 0;
  font-size: 0.857em;
  margin: 10px 0;
  width: 100%;
}

table table {
  font-size: 1em;
}

tr {
  border-bottom: 1px solid #ccc;
  padding: 0.1em 0.6em;
  background: #efefef;
  background: rgba(0, 0, 0, 0.063);
}

thead > tr {
  border-bottom: 1px solid #000;
}

tr.odd {
  background: #e4e4e4;
  background: rgba(0, 0, 0, 0.105);
}

table tr th {
  background: #757575;
  background: rgba(0, 0, 0, 0.51);
  border-bottom-style: none;
}

table tr th,
table tr th a,
table tr th a:hover,
table tr th a:focus {
  color: #fff;
  font-weight: bold;
}

table tbody tr th {
  vertical-align: top;
}

tr td,
tr th {
  padding: 4px 9px;
  border: 1px solid #fff;
  text-align: left;
  /* LTR */
}

[dir="rtl"] tr td,
[dir="rtl"] tr th {
  text-align: right;
}

/**
 * Responsive tables.
 */
@media screen and (max-width: 37.5em) {
  /* 600px */
  th.priority-low,
  td.priority-low,
  th.priority-medium,
  td.priority-medium {
    display: none;
  }
}

@media screen and (max-width: 60em) {
  /* 920px */
  th.priority-low,
  td.priority-low {
    display: none;
  }
}

/**
 * @file
 * Visual styles for Bartik's ui-widget component.
 */
#page .ui-widget {
  font-family: "Lato", sans-serif;
}

.ui-dialog {
  font-family: "Lato", sans-serif;
}

/**
 * @file
 * Theme styling for tabs.
 */
nav.tabs {
  margin-bottom: 20px;
}

/**
 * @file
 * Visual styles for a resizable textarea.
 */
.form-textarea-wrapper textarea {
  display: block;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
}

/**
 * @file
 * Theme styling for user module.
 */
/* Visual styling for the Password strength indicator */
.password-strength__meter {
  margin-top: 0.5em;
  background-color: #ebeae4;
}

.password-strength__indicator {
  background-color: #77b259;
  -webkit-transition: width 0.5s ease-out;
  transition: width 0.5s ease-out;
}

.password-strength__indicator.is-weak {
  background-color: #e62600;
}

.password-strength__indicator.is-fair {
  background-color: #e09600;
}

.password-strength__indicator.is-good {
  background-color: #0074bd;
}

.password-strength__indicator.is-strong {
  background-color: #77b259;
}

.password-confirm,
.password-field,
.password-strength,
.password-confirm-match {
  width: 55%;
}

.password-suggestions {
  padding: 0.2em 0.5em;
  margin: 0.7em 0;
  max-width: 34.7em;
  border: 1px solid #b4b4b4;
}

.password-suggestions ul {
  margin-bottom: 0;
}

.confirm-parent,
.password-parent {
  clear: left;
  /* LTR */
  margin: 0;
  max-width: 33em;
  overflow: hidden;
}

[dir="rtl"] .confirm-parent,
[dir="rtl"] .password-parent {
  clear: right;
}

/* Styling for the status indicator of the passwords match test.  */
.password-confirm .ok {
  color: #325e1c;
  font-weight: bold;
}

.password-confirm .error {
  color: #a51b00;
  font-weight: bold;
}

/* Login page stype */
.page-user-login main .region-content, .page-user-password main .region-content {
  max-width: 400px;
  background: white;
  padding: 15px 25px;
  margin: 0 auto;
  border-radius: 5px;
}

/**
 * @file
 * Styling for Views in Bartik.
 */
/* Tab styles */
.views-displays .tabs .open > a {
  border-radius: 7px 7px 0 0;
}

.views-displays .tabs .open > a:hover,
.views-displays .tabs .open > a:focus {
  color: #0071B3;
}

.views-displays .secondary .form-submit {
  font-size: 0.846em;
}

/* Tabs action list styles */
.views-displays .tabs .action-list {
  padding: 0;
}

/* Contextual filter options styles */
.views-filterable-options .filterable-option:nth-of-type(even) .form-type-checkbox {
  background-color: #F9F9F9;
}

/* Views action dropbutton styles */
.views-ui-display-tab-actions .dropbutton .form-submit {
  color: #0071B3;
}

.views-ui-display-tab-actions .dropbutton .form-submit:hover,
.views-ui-display-tab-actions .dropbutton .form-submit:focus {
  color: #018FE2;
}

/**
 * @file
 * Styles for Bartik's modal windows
 */
.ui-widget-overlay {
  background: #000;
  opacity: 0.7;
}

.ui-dialog {
  font-family: "Lato", sans-serif;
  border-radius: 0;
}

.ui-dialog input,
.ui-dialog select,
.ui-dialog textarea {
  font-size: 0.9em;
}

.ui-dialog .button {
  background-color: #fff;
  background-image: -webkit-linear-gradient(top, #f3f3f3, #e8e8e8);
  background-image: linear-gradient(to bottom, #f3f3f3, #e8e8e8);
  border: 1px solid #e4e4e4;
  border-bottom-color: #b4b4b4;
  border-left-color: #d2d2d2;
  border-right-color: #d2d2d2;
  color: #3a3a3a;
  cursor: pointer;
  font-size: 0.929em;
  font-weight: normal;
  text-align: center;
  padding: 0.250em 1.063em;
  border-radius: 1em;
}

/*
Decoration elements

Styleguide 2.banner.dupa
*/
.node .field.field--name-field-d-icon {
  display: block;
  margin: 0;
  float: none;
}

/*
